___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  margin: 0;
  padding: 20px;
  background: #e0e0e0; /* Light grey background */
  color: #333333; /* Dark grey text */
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  background: #ffffff; /* White card */
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

form {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  position: relative;
}

input,
button {
  padding: 10px;
  font-size: 16px;
  border: none;
  border-radius: 8px;
}

input {
  background: #e0e0e0; /* Light grey input */
  flex: 1;
  color: #333333;
}

input:focus {
  outline: 2px solid #f5a623; /* Pastel orange focus */
}

button {
  background: #f5a623; /* Pastel orange */
  color: #ffffff;
  cursor: pointer;
  transition: background 0.2s;
}

button:hover {
  background: #e0911e; /* Darker orange */
}

#autocomplete-suggestions {
  position: absolute;
  top: 100%;
  left: 0;
  background: #ffffff;
  border-radius: 8px;
  max-height: 200px;
  overflow-y: auto;
  width: 100%;
  z-index: 10;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.suggestion {
  padding: 10px;
  cursor: pointer;
  color: #333333;
}

.suggestion:hover {
  background: #f5a623;
  color: #ffffff;
}

section {
  margin: 20px 0;
}

.current-weather {
  background: #ffffff; /* White card */
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
  margin-bottom: 20px;
}

.current-weather p {
  margin: 5px 0;
  color: #333333;
}

.forecast-day {
  border-top: 1px solid #e0e0e0;
  padding: 10px 0;
}

.forecast-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.forecast-details p {
  margin: 5px 0;
  color: #333333;
}

.weather-icon {
  width: 40px;
  height: 40px;
  vertical-align: middle;
}

.toggle-forecast {
  display: block;
  margin: 10px 0;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  background: #f5a623; /* Pastel orange */
  color: #ffffff;
  border: none;
  border-radius: 8px;
  text-align: center;
  transition: background 0.2s;
}

.toggle-forecast:hover {
  background: #e0911e; /* Darker orange */
}

#daily-data.collapsed {
  display: none;
}

.loading {
  display: none;
  text-align: center;
  font-size: 16px;
  margin: 20px 0;
  color: #f5a623;
}

.loading::after {
  content: '';
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid #f5a623;
  border-radius: 50%;
  border-top-color: transparent;
  animation: spin 1s linear infinite;
  margin-left: 8px;
}

.error {
  display: none;
  color: #f5a623;
  text-align: center;
  margin: 10px 0;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
